<template>
<div class="cs_fwb">
    <div class="cs-head">
        <span class="el-dialog__title ha-right-dialog__title">警情通报</span>
        <div class="el-dialog__headerbtn"><i class="el-dialog__close el-icon-close"></i></div>
    </div>
    <div class="el-dialog__body  ha-dialog-right__body">
        <p>fsdfdsfsdfsdfsdfsdfds</p>
        <el-input
          type="textarea"
          :rows="6"
          placeholder="请输入内容"
          v-model="textarea">
        </el-input>
    </div>
    <div class="el-dialog__footer">
        <div class="dialog-footer">
            <button type="button" class="el-button el-button--default" @click="scclick"><span>删 除</span></button>
            <button type="button" class="el-button el-button--primary" @click="qrclick"><span>确认回复</span></button>
        </div>
    </div>
    <tankuangcomponent v-if="is_tkshow" :content="content"></tankuangcomponent>
</div>
</template>


<script>
    export default {
        props:{

        },
        components: {
            tankuangcomponent:function (resolve, reject) {
                require(['./tankuang.vue'],  resolve)
            }
        },
        data: function(){
            return {
                is_tkshow:false,
                content:{},
            }
        },
        methods: {
            qrclick(){
                this.is_tkshow=true;
                this.content={
                    button:"回复",
                    message:"注意",
                    fn:function(){
                        var Bus=global.eventHub;
                        Bus.$emit("qrclick");
                    }
                }
            },
            scclick(){
                var Bus=global.eventHub;
                Bus.$emit("scclick")
                this.is_tkshow=true;
                this.content={
                    button:"删除",
                    message:"注意",
                    fn:function(){
                        var Bus=global.eventHub;
                        Bus.$emit("qrclick");
                    }
                }
            }
        },

    }
</script>
<style scoped>
    .cs-head{
        height: 40px;
        background: #eef1f6;
        padding-left: 15px;
        line-height: 40px;
    }
    .cs_fwb{
        background: #fff;
        width: 700px;
        position: absolute;
        left: 50%;
        top: 30%;
        margin-left: -350px;
        z-index: 3000;
        border: 1px solid #e8e8e8;
    }
    .el-dialog__body .el-input{
        margin-bottom: 20px;
    }
    .el-dialog__body p{
        padding-bottom: 20px;
    }
    .el-dialog__headerbtn{
        padding-right: 20px;
    }
</style>


